.saved-products-container {
  width: 100%;
  height: 100%;
  padding: 0vh 4vw; 
  background-color: white;
  overflow-y: hidden;
}

.saved-products-container * {
  box-sizing: border-box;
}

.container-items-saved {
  max-height: 74vh;
  overflow-y: auto;
  padding: 2vh;
  border-radius: 0.4vh; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff; 
}

.page-title {
  font-size: 2vh;
  text-align: center;
  color: #333;
  padding: 0;
  margin: 1vh 0 0vh 0;
}

.product-item-save-products {
  background: #fff;
  border-radius: 0.4vh; 
  padding: 2vh;
  margin-bottom: 2vh;
  box-shadow: 0 0.6vh 1.8vh rgba(0, 0, 0, 0.251);
}

.product-title {
  font-size: 1.8vh;
  margin-bottom: 1vh;
  color: #2c3e50;
}

/* ID del producto */
.product-id {
  font-size: 1.4vh;
  color: #666;
  margin-bottom: 1vh;
}

/* Título de la sección de plantillas */
.templates-title {
  font-size: 1.6vh;
  color: #333;
}

/* Lista de plantillas: con alto máximo para scroll */
.template-list-asign {
  max-width: 100%;
  list-style: none;
  padding: 0;
  margin: 0 0 2vh 2vw;
  max-height: 28vh; 
  overflow-y: auto;
  overflow-x: hidden;
}

.template-list-asign li:nth-child(even) {
  background-color: #e8f0fe; /* azul muy claro */
}

.template-list-asign li:nth-child(odd) {
  background-color: #dbeafe; /* un poco más fuerte */
}

/* Cada item de plantilla */
.template-item-products-save {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.8vh;
  border-radius: 0.4vh; 
  color: #2c3e50;
  margin: 0.4vh;
  border-bottom: 0.1vh solid #ddd;
  font-size: 1.6vh;
  transition: background 0.3s ease-in-out;
}

.template-item-products-save:hover {
  background-color: #dfe4eabd; 
}

.container-buttons-products-saved{
  display: flex;
  flex-direction: row;
  gap: 4vh;
}

/* Botón para eliminar plantilla */
.delete-template-btn {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: transform 0.2s ease, color 0.2s ease;
}

.delete-template-btn:hover {
  transform: scale(1.2); /* Pequeño efecto de escala */
  color: #e60000e1;
}

/* Contenedor de la información y botones de movimiento */
.template-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Botones para mover plantilla */
.move-buttons {
  display: flex;
  flex-direction: column;
}

.move-template-btn {
  background: none;
  border: none;
  padding: 4px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.move-template-btn:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.move-template-btn:hover:not(:disabled) {
  transform: scale(1.2);
}

.quit-modal-btn{
  background-color: #ff0000;
  color: #fff;
  font-size: 1.4vh;
  border: none;
  padding: 0.8vh 1.2vh;
  margin-top: 2vh;
  margin-left: 6vh;
  font-weight: bold;
  border-radius: 1.8vh; 
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.quit-modal-btn:hover {
  background-color: #b30000;
}

.open-modal-btn {
  background-color: #007bff;
  color: #fff;
  font-size: 1.4vh;
  border: none;
  padding: 0.8vh 1.2vh;
  font-weight: bold;
  margin-top: 2vh;
  border-radius: 1.8vh; 
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.open-modal-btn:hover {
  background-color: #0056b3;
}

.open-second-modal-btn{
  background-color: #c3ff00;
  color: #181818;
  font-size: 1.4vh;
  border: none;
  padding: 0.8vh 1.2vh;
  font-weight: bold;
  border-radius: 1.8vh; 
  cursor: pointer;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: background-color 0.3s ease-in-out;
}

.open-second-modal-btn:hover {
  background-color: #b3a700;
  color: white;
}


.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(0.2vh);
  z-index: 1000;
}

.modal-content {
  background: #fff;
  padding: 2vh;
  border-radius:0.2vh;
  min-width: 60vw;
  width: 100%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.templates-checkbox-list {
  margin: 1.6vh 0;
  height: 40vh;
  overflow-y: auto;
  padding-right: 1vh;
  scrollbar-width: thin;
  scrollbar-color: #007bff #f0f0f0;
}

.template-checkbox-label:nth-child(odd) {
  background-color: #f0f4f8b3;
}

.template-checkbox-label:nth-child(even) {
  background-color: #ffffffba;
}

.template-checkbox-label:hover {
  background-color: #dfe4eabd; 
}


.templates-checkbox-list::-webkit-scrollbar {
  width: 6px;
}

.templates-checkbox-list::-webkit-scrollbar-thumb {
  background: #007bff;
  border-radius: 3px;
}

.template-checkbox-label {
  display: block;
  margin-bottom: 1vh;
  font-size: 1.6vh;
  cursor: pointer;
  transition: color 0.3s ease;
  align-items: center;
  padding: 0.8vh;
  border-radius: 0.6vh;
  border-bottom: 0.1vh solid #ddd;
  transition: background 0.3s ease-in-out;
}

.template-checkbox-label:hover {
  color: #007bff;
}

.modal-buttons-products-saved {
  display: flex;
  justify-content: space-between;
  gap: 1vh;
  margin-top: 2vh;
  border-radius: 1.8vh;
}

.container-father-templates-show{
  display: flex;
  gap: 2vh;
  flex-direction: row;
}

.container-child-templates-show{
  min-width: 50%;
  width: 100%;
  position: relative;
}

.container-second-message{
  border-radius: 0.2vh;
  min-height: 100%;
}

.variant-section{
  padding-left: 4vh;
}

.add-template-btn,
.close-modal-btn {
  border: none;
  padding: 1vh 2vh;
  font-size: 1.6vh;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-template-btn {
  background-color: #007bff;
  color: #fff;
  border-radius: 1.8vh;
}

.add-template-btn:hover {
  background-color: #0056b3;
}

.close-modal-btn {
  background-color: #ff4d4d;
  color: #fff;
  border-radius: 1.8vh;
}

.close-modal-btn:hover {
  background-color: #b30000;
}

.empty-second-messages-title{
  font-size: 1.4vh;
  color: grey;
}

.content-child-second-message-show ul{
  padding-left: 1vh;
}

.content-child-second-message-show ul li{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 3vh 1vh;
  border-radius: 0.4vh;
  color: #2c3e50;
  margin: 0.4vh;
  border-bottom: 0.1vh solid #ddd;
  font-size: 1.6vh;
  transition: background 0.3s ease-in-out;
}

.content-child-second-message-show ul li:nth-child(even) {
  background-color: #dbeafe;
}

.content-child-second-message-show li:nth-child(odd) {
  background-color: #e8f0fe;
}

/* Responsividad */
@media (max-width: 480px) {
  .modal-content {
    width: 95%;
    max-width: 360px;
    padding: 3vh;
  }

  .modal-buttons-products-saved {
    flex-direction: column;
  }

  .add-template-btn,
  .close-modal-btn {
    width: 100%;
  }
}

.error-text {
  color: red;
}

.spinner {
  background-color: white;
  display: block;
  margin: auto;
}

/* Ajustes responsivos para pantallas más pequeñas */
@media (max-width: 768px) {
  .saved-products-container {
    padding: 2vh 3vw;
  }
  .page-title {
    font-size: 1.6vh;
  }
  .product-item-save-products {
    padding: 2vh;
  }

  .template-id-input,
  .delete-template-btn,
  .add-template-btn {
    font-size: 2vh;
  }

  .add-template-container {
    flex-direction: column;
    gap: 2vh;
  }
}

/* ─── Contenedor general del delay ───────────────────────────────────────── */

.content-titles-second-messages{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.delay-container {
  display: flex;
  flex-direction: column;
  margin-right: 1vh;
  font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

/* Etiqueta (label) */
.delay-label {
  font-size: 1.2vh;
  font-weight: 600;
  color: #333333;
  margin-bottom: 0.25rem;
}

/* ─── Grupo para input + valor por defecto ───────────────────────────────── */

.delay-input-group {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* Campo de entrada editable */
.delay-input {
  width: 3vw;            /* ancho fijo para centrar número */
  padding: 0.6vh 0.8vh;  /* espacio interior cómodo */
  border: 1px solid #cccccc;
  border-radius: 0.2vh;  /* esquinas ligeramente redondeadas */
  font-size: 1.4vh;
  color: #222222;
  background-color: #ffffff;
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.delay-input:focus {
  outline: none;
  border-color: #007bff;    /* tono azul corporativo al enfocar */
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.2);
}

/* Texto que muestra el valor por defecto */
.delay-default {
  font-size: 1.4vh;
  color: #555555;
  background-color: #f2f2f2;
  padding: 0.6vh 0.8vh;
  border-radius: 0.375rem;
  border: 1px solid #dddddd;
  display: inline-flex;
  align-items: center;
}

.delay-default:hover {
  background-color: #e8e8e8;
}

/* Opcional: responsivo para pantallas muy angostas */
@media (max-width: 480px) {
  .delay-input-group {
    flex-direction: column;
    align-items: flex-start;
  }
  .delay-input {
    width: 100%;
  }
  .delay-default {
    margin-top: 0.5rem;
  }
}

.delay-apply-btn {
  padding: 0.8vh 1vh;
  background-color: #0052cc;
  color: #ffffff;
  font-size: 1.2vh;
  font-weight: 400;
  border: none;
  border-radius: 0.2vh;
  cursor: pointer;
  transition:
    background-color 0.25s ease,
    box-shadow 0.25s ease,
    transform 0.1s ease;
  box-shadow: 0 2px 4px rgba(0, 82, 204, 0.15);
  text-align: center;
  letter-spacing: 0.3px;
  position: relative;
}

.delay-apply-btn:hover:not(:disabled) {
  background-color: #003d99; /* más oscuro al pasar el mouse */
  box-shadow: 0 4px 8px rgba(0, 82, 204, 0.25);
  transform: translateY(-0.52px);
}

.delay-apply-btn:active:not(:disabled) {
  background-color: #002f73;
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(0, 82, 204, 0.2);
}

.delay-apply-btn:disabled {
  background-color: #cfd8e3;
  color: #888888;
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

/* Icono opcional de carga */
.delay-apply-btn.loading::after {
  content: "";
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  border: 2px solid white;
  border-top-color: transparent;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

/* Spinner animation */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}